


.add-to-cart-quanity-counter{
  width: 120px;
  height: 48px;
  background-color: var(--color-white-500);

  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr;

  text-align: center;


  .plus, .minus{
    position: relative;
    cursor: pointer;
  }

  .minus::after,
  .plus::before{
    content: "";

    position: absolute;

    width: 0px;
    height: 0px;
    padding: 20px;

    left: 50%;
    top: 50%;

    transform: translate(-50%, -50%);
  }
}