.general-button{
  display: block;
  cursor: pointer;
  width: fit-content;
  padding: 15px 30px;

  white-space: nowrap;
}

.general-button:hover{
  transition: var(--ui-transition-time);
}




.general-button-1{
  background-color: var(--color-orangebrown);
  color: var(--color-white-100);
  border: none;
}

.general-button-1:hover{
  background-color: var(--color-orangebrown-hover);
}

.general-button-2{
  background-color: transparent;
  border: 1px solid var(--color-black-900);
  color: var(--color-black-900);
}

.general-button-2:hover{
  background-color: var(--color-black-900);
  color: var(--color-white-100);
}


.general-button-2-alt{
  background-color: var(--color-black-900);
  color: var(--color-white-100);
  border: none;
}

.general-button-2-alt:hover{
  background-color: var(--color-grey-500);
  color: var(--color-white-100);
}