





.product-preview-container{
  flex-direction: column;
  text-align: center;
}


.product-preview-image{
  display: block;
  width: 100%;
  border-radius: var(--standard-border-radius);
}


.product-preview-additional-content-container{
  margin-top: calc(var(--margin-unit) * 8);
  flex-direction: column;
  row-gap: calc(var(--margin-unit) * 6);


  .product-preview-title{
    max-width: 20ch;
  }


  .product-preview-description{
    max-width: 35ch;
  }
}




@media screen and (min-width: 475px){

  .product-preview-additional-content-container{
    margin-top: calc(var(--margin-unit) * 13);
    flex-direction: column;
    row-gap: 0;  
  
    .product-preview-title{
      margin-top: calc(var(--margin-unit) * 4);
    }
  
  
    .product-preview-description{
      margin-top: calc(var(--margin-unit) * 8);
      max-width: 60ch;
    }

    .product-preview-button{
      margin-top: calc(var(--margin-unit) * 6);
    }
  }

}


@media screen and (min-width: 800px){

  .product-preview-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: calc(var(--margin-unit) * 31);
    text-align: left;

    .product-preview-additional-content-container{
      margin-top: 0;
      align-items: start;

    }
  
    .product-preview-title{
      margin-top: calc(var(--margin-unit) * 4);
    }
  
  
    .product-preview-description{
      margin-top: calc(var(--margin-unit) * 8);
      max-width: 45ch;
    }

    .product-preview-button{
      margin-top: calc(var(--margin-unit) * 10);
    }
  }

}