






.homepage-product-categories-grid{
  margin-top: calc(var(--margin-unit) * 23);
}


.homepage-about-us-insert{
  margin-top: calc(var(--margin-unit) * 30);
}


@media screen and (min-width: 475px){

  .homepage-product-categories-grid{
    margin-top: calc(var(--margin-unit) * 38);
  }

  .homepage-about-us-insert{
    margin-top: calc(var(--margin-unit) * 24);
  }
}


@media screen and (min-width: 800px){

  .homepage-product-categories-grid{
    margin-top: calc(var(--margin-unit) * 50);
  }

  .homepage-about-us-insert{
    margin-top: calc(var(--margin-unit) * 50);
  }
}