

.product-page-container{
  flex-direction: column;
  text-align: left;
  align-items: start;

  > *{
    width: 100%;
  }
}

.product-page-go-back-link{
  margin-top: calc(var(--margin-unit) * 4);
}

.product-page-product-image{
  margin-top: calc(var(--margin-unit) * 6);
  border-radius: var(--standard-border-radius);
  width: 100%;
}

.product-page-product-main-cta{
  margin-top: calc(var(--margin-unit) * 8);
  flex-direction: column;
  align-items: start;

  .product-page-overline{

  }
  
  .product-page-title{
    margin-top: calc(var(--margin-unit) * 6);
  }
  
  .product-page-product-description{
    margin-top: calc(var(--margin-unit) * 6);
  }
  
  .product-page-product-price{
    margin-top: calc(var(--margin-unit) * 6);
  }
  
  .product-page-purchase-buttons-container{
    margin-top: calc(var(--margin-unit) * 8);
    column-gap: calc(var(--margin-unit) * 4);
  }
}

.product-page-product-features-section{
  margin-top: calc(var(--margin-unit) * 22);
  flex-direction: column;
  align-items: start;

  .product-page-features-subtitle{

  }

  .product-page-features-features-1{
    margin-top: calc(var(--margin-unit) * 6);
  }

  .product-page-features-features-2{
    margin-top: calc(var(--margin-unit) * 7);
  }
}


.product-page-in-the-box-section{
  margin-top: calc(var(--margin-unit) * 22);
  flex-direction: column;
  align-items: start;

  .product-page-in-the-box-subtitle{
  }

  ul{
    margin-top: calc(var(--margin-unit) * 6);
    li{
      margin-top: calc(var(--margin-unit) * 2);
      list-style: none;

      justify-content: start;
      column-gap: calc(var(--margin-unit) * 6);
      p{
        display: inline;
      }
    }
  }
}

.product-page-additional-images-grid{
  margin-top: calc(var(--margin-unit) * 22);
  row-gap: calc(var(--margin-unit) * 5);
  flex-direction: column;
  img{
    width: 100%;
    border-radius: var(--standard-border-radius);
  }
}



.product-page-you-may-also-like-section{
  margin-top: calc(var(--margin-unit) * 30);
  flex-direction: column;


  .you-may-also-like-subtitle{
  }

  .you-may-also-like-grid{
    margin-top: calc(var(--margin-unit) * 10);
    flex-direction: column;
    row-gap: calc(var(--margin-unit) * 14);

    .you-may-also-like-grid-item{
      flex-direction: column;

      .you-may-also-like-product-title{
        margin-top: calc(var(--margin-unit) * 8);
      }
  
      img{
        width: 100%;
        border-radius: var(--standard-border-radius);
      }
  
      .you-may-also-like-product-link{
        margin-top: calc(var(--margin-unit) * 9);
      }
    }
  }
}

.product-page-product-categories-grid{
  margin-top: calc(var(--margin-unit) * 43);
}

.product-page-product-about-us-insert{
  margin-top: calc(var(--margin-unit) * 30);
}



@media screen and (min-width: 475px){
  
  .product-page-go-back-link{
    margin-top: calc(var(--margin-unit) * 8);
  }

  .product-page-main-section{
    margin-top: calc(var(--margin-unit) * 6);

    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: calc(var(--margin-unit) * 17);
  }

  .product-page-product-main-cta{
    margin-top: 0;
  }

  .product-page-product-image{
    margin-top: 0;
    height: 100%;
    object-fit: cover;
  }

  .product-page-title{
    margin-top: calc(var(--margin-unit) * 4);
  }

  .product-page-product-description,
  .product-page-product-price{
    margin-top: calc(var(--margin-unit) * 8);
  }

  .product-page-product-features-section{
    margin-top: calc(var(--margin-unit) * 30);

    .product-page-features-features-1{
      margin-top: calc(var(--margin-unit) * 8);
    }
  }





  .product-page-in-the-box-section{
    margin-top: calc(var(--margin-unit) * 30);
    display: grid;
    grid-template-columns: 1fr 1fr;

    ul{
      margin-top: 0;

      li:first-child{
        margin-top: 0;
      }
    }
  }


  .product-page-additional-images-grid{
    margin-top: calc(var(--margin-unit) * 30);
    gap: calc(var(--margin-unit) * 5);

    display: grid;
    grid-template-columns: 2fr 3fr;
    grid-template-rows: 1fr 1fr;

    img{
      height: 100%;
    }

    img:nth-child(3){
      grid-row: 1 / 3;
      grid-column: 2 / 3;
    }
  }


  .product-page-you-may-also-like-section{
    overflow-x: hidden;
    .you-may-also-like-grid{
      margin-top: calc(var(--margin-unit) * 14);
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: clamp(calc(var(--margin-unit) * 1), 2vw, calc(var(--margin-unit) * 3));

      > *{
        height: 100%;
      }

      .you-may-also-like-grid-item{
        .you-may-also-like-product-title{
          margin-top: calc(var(--margin-unit) * 10);
        }

        .you-may-also-like-product-link{
          margin-top: calc(var(--margin-unit) * 8);
        }
      }
    }
  }
}



@media screen and (min-width: 800px){

  .product-page-go-back-link{
    margin-top: calc(var(--margin-unit) * 20);
  }

  .product-page-main-section{
    margin-top: calc(var(--margin-unit) * 14);
    column-gap: calc(var(--margin-unit) * 31);

    .product-page-product-description{
      max-width: 45ch;
    }
  
    .product-page-purchase-buttons-container{
      margin-top: calc(var(--margin-unit) * 12);
    }
  }


  .product-page-secondary-section{
    margin-top: calc(var(--margin-unit) * 40);
    column-gap: calc(var(--margin-unit) * 31);
    
    display: grid;
    align-items: start;
    grid-template-columns: 2fr 1fr;

    .product-page-in-the-box-section{
      display: flex;
    }

    .product-page-product-features-section,
    .product-page-in-the-box-section{
      margin-top: 0;
    }

    .product-page-in-the-box-section{
      ul{
        li{
          margin-top: calc(var(--margin-unit) * 2);
        }
      }
    }
  }

  .product-page-additional-images-grid{
    margin-top: calc(var(--margin-unit) * 40);
    gap: calc(var(--margin-unit) * 8);
  }

  .product-page-you-may-also-like-section{
    margin-top: calc(var(--margin-unit) * 40);
    .you-may-also-like-grid{
      margin-top: calc(var(--margin-unit) * 16);
      gap: calc(var(--margin-unit) * 8);

      .you-may-also-like-grid-item{
        .you-may-also-like-product-link{
          margin-top: calc(var(--margin-unit) * 12);
        }
      }
    }

  }

  .product-page-product-categories-grid{
    margin-top: calc(var(--margin-unit) * 60);
  }
  
  .product-page-product-about-us-insert{
    margin-top: calc(var(--margin-unit) * 40);
  }
}



.button-add-to-cart-animation{
  animation: add-to-cart 1s ease-out;
}



@keyframes add-to-cart {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}