

.product-category-page-product-preview-items-container{
  flex-direction: column;
  margin-top: calc(var(--margin-unit) * 16);
  row-gap: calc(var(--margin-unit) * 30);
}


.product-category-page-product-categories-grid{
  margin-top: calc(var(--margin-unit) * 43);
}


.product-category-page-about-us-insert{
  margin-top: calc(var(--margin-unit) * 30);
}


@media screen and (min-width: 475px){
  .product-category-page-product-preview-items-container{
    margin-top: calc(var(--margin-unit) * 30);
  }
}


@media screen and (min-width: 800px){
  .product-category-page-product-preview-items-container{
    margin-top: calc(var(--margin-unit) * 40);
    row-gap: calc(var(--margin-unit) * 40);

    > *:nth-child(even){
      > :first-child{
        order: 2;
      }
    }
  }

  .product-category-page-product-categories-grid{
    margin-top: calc(var(--margin-unit) * 60);
  }

  .product-category-page-about-us-insert{
    margin-top: calc(var(--margin-unit) * 40);
  }

}