
.navbar{
  width: 100%;
  height: var(--navbar-height);
  background-color: var(--color-black-700);
  z-index: var(--z-index-higher);
  position: relative;
}

.navbar-position-absolute{
  position: absolute;
}

.navbar-content{
  justify-content: space-between;
  color: var(--color-white-100);
  height: 100%;
}

.navbar-hamburger-icon{
  height: 100%;
  cursor: pointer;
  
  position: relative;
}

.navbar-hamburger-icon::after{
  content: "";

  position: absolute;

  width: 0px;
  height: 0px;
  padding: 25px;

  left: 50%;
  top: 50%;

  display: block;

  transform: translate(-50%, -50%);
}

.navbar-navlinks{
  display: none;
}

.navber-menu-and-logo-container{
  position: relative;
  width: 50%;
  justify-content: flex-start;
  gap: 34px;

}

.navbar-navlink{
  text-decoration: none;
}

.navbar-logo-link{
  position: absolute;
  right: 0;
  transform: translateX(50%);

  height: 250%;
}

.navbar-shopping-cart{
  cursor: pointer;
  position: relative;
}

.navbar-shopping-cart::after{
  content: "";

  position: absolute;

  width: 0px;
  height: 0px;
  padding: 25px;

  left: 50%;
  top: 50%;

  display: block;

  transform: translate(-50%, -50%);
}


.navbar-underline{
  height: 1px;
  background-color: #434343;
  width: 100%;
  margin: 0 auto;
}

.navbar-underline-hidden{
  display: none;
}


.navbar-dropdown-menu{
  padding-top: calc(var(--margin-unit) * 21);
  padding-bottom: calc(var(--margin-unit) * 9);
  background-color: var(--color-white-100);
  position: absolute;
  width: 100%;
  top: 100%;

  border-bottom-left-radius: var(--standard-border-radius);
  border-bottom-right-radius: var(--standard-border-radius);

}


@media screen and (min-width: 475px){

  .navber-menu-and-logo-container{
    width: unset;
  }

  .navbar-logo-link{
    position: unset;
    transform: unset;
    height: 1%;
  }


  .navbar-underline{
    width: calc(100% - calc(2 * var(--wrapper-inline-padding)));
  }

  .navbar-dropdown-menu{
    padding-top: calc(var(--margin-unit) * 27);
    padding-bottom: calc(var(--margin-unit) * 17);
  }
}

@media screen and (min-width: 800px){
  .navbar-hamburger-icon{
    display: none;
  }

  .navbar-navlinks{
    display: flex;
    gap: 34px;
  }

  .navbar-logo-link{
    position: unset;
    transform: unset;
  }

  .navbar-underline{
    max-width: calc(var(--wrapper-max-width) - calc(2 * var(--wrapper-inline-padding)));
  }
}
