




.category-header{
  --category-header-additional-height: 100px;
  height: var(--category-header-additional-height);
  background-color: var(--color-black-900);
  position: relative;
  
  .category-header-h2{
    left: 50%;
    top: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
  }
}

@media screen and (min-width: 475px){
  .category-header{
    --category-header-additional-height: 250px;
  }
}

@media screen and (min-width: 800px){
  .category-header{
    --category-header-additional-height: 240px;
  }
}